var xportfolio = angular.module('xportfolio', [
  'firebase',
  'ui.router',
  'iso.directives',
  'ngAnimate',
  'angulartics',
  'angulartics.google.analytics'
]);

xportfolio.config(function($stateProvider, $urlRouterProvider, $analyticsProvider) {
  
  //$locationProvider.html5Mode(true);

  $analyticsProvider.firstPageview(true); /* Records pages that don't use $state or $route */
  $analyticsProvider.withAutoBase(true);

  $urlRouterProvider.otherwise("/intro");
  
  $stateProvider
    .state('intro', {
      url: "/intro",
      templateUrl: "templates/intro.html"
    })
    .state('portfolio', {
      url: "/portfolio",
      templateUrl: "templates/portfolio.html",
    })
    .state('bio', {
      url: "/bio",
      templateUrl: "templates/bio.html",
    })
    .state('project', {
      url: "/project/:projectID",
      templateUrl: "templates/project.html",
    });
});


xportfolio.animation('.slide', [function() {
  return {
    enter: function(element, doneFn) {
      jQuery(element).fadeIn(500, doneFn);
    },
    leave: function(element, doneFn) {
      jQuery(element).fadeOut(500, doneFn);
    }
  }
}]);
xportfolio.controller("bioController", function($scope) {
    console.log("bio");
});
xportfolio.controller("introController", function($scope) {
    console.log("intro");
});
xportfolio.controller("portfolioController", function($scope, portfolioProjectsService, $state) {
    console.log("portfolio");

    $scope.projects = portfolioProjectsService.getAllProjects();

    $scope.$emit('iso-method', {name:'packery', params:null});

    $scope.projectXY = function(project){
    	return "x-" + project.width; // + " y-"+project.height;
    }

    $scope.projectThumb = function(project){
    	return "./images/"+project.image_prefix+"_thumb"+".jpg";
    }

    $scope.showProject = function(index){
        $state.go("project", {projectID:index});
    }
});
xportfolio.controller("projectController", function($scope, $stateParams, portfolioProjectsService) {
    $scope.projectId = $stateParams.projectID;
    $scope.project = portfolioProjectsService.getProject($scope.projectId);
    console.log("project:", $scope.projectId, $scope.project);

    $scope.projectThumb = function(project){
    	return "./images/"+project.image_prefix+"_thumb"+".jpg";
    }

});
xportfolio.service('portfolioProjectsService', function($firebaseObject) {
    this.getProject = function(index) {
            return $firebaseObject(new Firebase("https://xportfolio.firebaseio.com/projects/"+index));
        }
 
    this.getAllProjects = function() {
            return $firebaseObject(new Firebase("https://xportfolio.firebaseio.com/projects/"));
        }
});